@import '../../assets/sass/flexbox';

#pagination {
    position: fixed;
    margin-top: 20px;
}

.pagination {
    display: inline-block;
    background-color: #F8EDB0;
  }
  
.pagination a {
    color: rgb(30, 28, 28);
    float: left;
    padding: 8px 16px;
    text-decoration: none;
    border: 1px solid #ddd;
}

.pagination a.active {
    color: white;
}


.pagination a:first-child {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.pagination a:last-child {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

#paginator-wrapper {
    margin-left: -35px;
    list-style: none;
    @include flexbox;
    @include flex-direction(column);

    li {
        @include flex(1);
        text-align: center;
        margin: 0 10px 10px 0;
    }
}

@media (min-width: 768px) {
    #pagination {
        position: inherit;
        margin-top: 60px;
    }
    
    #paginator-wrapper {
        @include flex-direction(row);
    }
    .pagination {
        margin: 0 0 0 5px;
    }

    #paginator-wrapper {
        margin-top: 10px;
    }
}