@import '../../assets/sass/flexbox';

.header-menu {
    @include flexbox;
    @include flex-direction(row);
    background-color: #47332D;

    .aside-left, .aside-right {
        @include flex(1);
    }

    // .aside-right {
    //     div * {
    //         float: right;
    //         margin-right: 1%;
    //     }
    // }

    #header-menu-nav {
        display: none;
    }

    .menu-middle { 
        @include flex(4);

        nav .float-right {
            margin-bottom: 3px;

            * {
                float: right;
                margin: 0 -20px 5px 0;
            }
        }

        div {
            position: relative;

            .link-name {
                margin: 0 0 0 7px;
                font-size: 22px;
            }

            a:hover {
                span {
                    color: #F8EDB0;
                }
            }

            a span {
                color: whitesmoke; 

                * {
                    margin: 5px 0 5px 15px;
                }
    
                .ico {
                    width: 30px;
                    height: 30px;
                }
            }
        }
    }

}


@media (min-width: 630px) {
}

@media (min-width: 876px) {

    .header-menu {

        #header-menu-nav {
            text-align: center;
            display: inherit;

            nav {
                display: block;
                margin: 0 auto;
            }
        }
        
        .menu-middle { 

            nav .float-right {
                * {
                    margin: 0 -25px 0 0;
                }
            }

            div {
                display: inline-block;
                
                .link-name {
                    margin: 0 33px 0 7px;
                }
            }
        }
    }
}

