
@import '../../assets/sass/flexbox';

.paginator-article {
    // border: 1px solid rgba(71, 51, 45, 0.9);
    background-color: rgba(255, 255, 255, 0.6);
    @include flexbox;
    @include flex-direction(column);
    width: 97%;
    padding: 1px;

    .image-wrapper {
        @include flex(1);

        .container-wrapper {
            @include flexbox;

            .left {
                @include flex(1);

                .article-title {
                    color: #47332D;
                }
            }

            .right {
                @include flex(1);
    
                img.bottle {
                    @include flex(1);

                    margin-top: 15px;
                } 
            }
        }
    }

    .article-description {
        @include flex(1);
        text-align: left;
    }
}

@media (min-width: 768px) {
    .paginator-article {
        @include flex-direction(row);
    }
}