#nav-links {
    display: none;

    .link-group {
        margin-top: 0;
        background-color: rgb(248, 237, 176);
        list-style: none;
        padding: 5px 0 0 6px;
        text-align: center;

        li {
            width: 60px;
            height: 60px;
            display: inline-block;
            margin: 0 4px 5px 0;
            padding-top: 5px;

            &:hover, &:active, &:focus {
                background-color: white;
            }
        }
    }
}


@media (min-width: 630px) {
    #nav-links {
        display: inherit;
    }
}

