@import '../../assets/sass/flexbox';

.logo-header {
    @include flexbox;
    @include flex-direction(row);
    background-color: #47332D;
    // background-image: url("../../assets/images/bg-part.jpg");
    // background-size: 25%;

    .aside-left, .aside-right {
        @include flex(1);
    }

    .aside-left {
        padding: 5px 0 0 2px;
    }

    .aside-right {
        .lang-btn {
            background-color: #47332D;
            border: 1px solid #47332D;
            margin: 3px 0 0 5px;
            padding-top: 5px;
            outline: none;
        }
    }

    .logo-wrapper {
        @include flex(4);

        div {
            margin: 0;
            height: 100%;
            
            a.facebook {
                position: absolute;
                display: none;
            }
        }
    }
}

.logo-header .aside-left {
    #beers-btn {
        float: left;
        margin: 1px 0 2px 7%;
        margin-left: 11px;
        cursor: pointer;
        
        span * {
            color: #F5B61D;
            width: 33px;
            height: 33px;
        }
    }
}

.logo-header .aside-right {
    #menu-btn {
        float: right;
        margin: 10px 26% 0 0;
        cursor: pointer;
        
        span {
            background-color: #F5B61D;
            display: block;
            width: 28px;
            height: 4px;
            margin-bottom: 6px;
            -webkit-transition: all 0.5s linear;
            transition: all 0.3s linear;
        }
        &.open {
            span {
                &:nth-child(1),
                &:nth-child(3) {
                    transform: translate(0px, 13px) rotate(-45deg) scalex(1.1);
                    margin: 0;
                }
                &:nth-child(2) {
                    height: 0;
                    margin: 0;
                }
                &:nth-child(3) {
                    transform: translate(0px, 9px) rotate(45deg) scalex(1.1);
                }
            }
        }
    } // menu-bar-btn 
}

@media (min-width: 630px) {
    .logo-header .aside-left {
        #beers-btn {
            display: none;
        }
    }

    .logo-header {
        @include flexbox;
        @include flex-direction(row);
        background-color: #47332D;
    
        .aside-left, .aside-right {
            @include flex(1);
        }
    
        .aside-right {
            .lang-btn {
                margin: 3px 0 0 5px;
                padding-top: 5px;

                img {
                    width: 40px;
                    height: 28px;
                }
            }
        }
    }
}

@media (min-width: 876px) {
    .logo-header .aside-right {
        #menu-btn {
            display: none;
        }
    }
    .logo-header {
        .logo-wrapper {
            div a.facebook {
                display: inherit;
            }
        }
    }
}