.contacts {
    text-align: center;

    .contacts-wrapper {
        width: 99%;
        min-height: 400px;
        display: block;
        margin: 0 auto;
        text-align: center;
        background-color: rgba(255, 255, 255, 0.9);

        article span {
            font-size: 30px;
        }
    }
}

@media (min-width: 630px) {
    .contacts .contacts-wrapper{
        width: 66%;
        min-height: 400px;
    }
}