.beers-filter {
    text-align: center;

    .filter-btn {
        margin: 5px;
        min-width: 100px;
        height: 30px;
        font-size: 16px;
        color: #47332D;
        background-color: #F8EDB0;

        &:hover, &:active, &:focus {
            color: whitesmoke;
            background-color: #47332D;
        }
    }
}


@media (min-width: 630px) {

    #article-buttons-wrapper {
        float: right;
        margin-right: 5%;
    }
}