@import '../../assets/sass/flexbox';

.footer {
    @include flexbox;
    @include flex-direction(row);

    .footer-logo {
        @include flex(2);
        margin-top: -20px;
    }

    .footer-contacts {
        @include flex(1);
    
        .copy {
            color: whitesmoke;
            position: relative;
            top: 11px;
            left: -10px;
            font-size: 15px;
        }

        a#facebook {
            position: absolute;
            left: 0;
            padding-top: 30px;
        }
    }
}

@media (min-width: 430px) {
    .footer {
        height: 80px;

        .footer-contacts {
    
            a#facebook {
                position: inherit;
            }
        }
    }
}

@media (min-width: 864px) {
    .footer {
        height: 100px;
        
        .footer-contacts {
            padding-left: 65%;
        }

        #footer-logo {
            width: 100%;
        }
    }
}

@media (min-width: 1300px) {
    .footer {
        height: 110px;
    }
}
