@import '../../assets/sass/flexbox';

.main .home {
    @include flexbox;
    @include flex-direction(column);
    margin-top: 50px;

    .shop {
        @include flex(1);

        h4, h3, h1 {
            text-align: center;
            color: #47332D;
        }
    }

    .map {
        @include flex(1);
        text-align: center;

        a img {
            width: 70%;
        }

        .palivec-number {
            position: relative;
            top: -80px;
            left: -20px;
            color: #47332D;
            font-size: 40px
        }
    }
}

@media (min-width: 840px) {
    .main  .home {
        @include flex-direction(row);

        .shop {
        }

        .map {
            padding-top: 60px;

            a {
                img {
                    width: 72%;
                }
            }

            .palivec-number {
                top: -120px;
                left: -30px;
            }
        }
    }
}